function BlockElem(props) {
  return(
    <div>
      <img src={`./assets/img/thumb-${props.img}`} alt={props.name} className="w3-image" />
      <h3>{props.name}</h3>
    </div>
  );
}

class PortfolioSection extends React.Component{
  constructor(props) {
    super(props);
    
    this.state = {
      projectId: this.props.projects[0].id,
      projectName: this.props.projects[0].name,
      projectImg: this.props.projects[0].img,
      projectDate: this.props.projects[0].date,
      projectEnterprise: this.props.projects[0].enterprise,
      projectDetails: this.props.projects[0].details,
      isVisible: false
    }
  }

  displayProject = (thisElem, idParam, nameParam, dateParam, enterpriseParam, imgParam, detailsParam) => {
    this.setState({
      projectId: idParam,
      projectName: nameParam,
      projectImg: imgParam,
      projectDate: dateParam,
      projectEnterprise: enterpriseParam,
      projectDetails: detailsParam,
      isVisible: true
    })
  }

  hideBlock = (e) => {
    e.preventDefault()
    this.setState({
      isVisible: false
    })
  }

  render() {
    let blocks = [],
        detailsBlock,
        showGrid = ''
    const finalArr = [],
          projectName = this.state.projectName,
          projectDate = this.state.projectDate,
          projectEnterprise = this.state.projectEnterprise,
          projectImg = this.state.projectImg,
          projectDetails = this.state.projectDetails,
          isVisible = this.state.isVisible

    this.props.projects.forEach((project, index) => {
      let handleClick = (e) => {
        e.preventDefault()
        this.displayProject(this, project.id, project.name, project.date, project.enterprise, project.img, project.details)
      }  
      blocks.push(
        <div key={index} className="w3-col s6 m3 l3 w3-padding">
          <a href="#portfolio" onClick={handleClick} name={project.name} date={project.date} enterprise={project.enterprise} img={project.img} details={project.details}>
            <BlockElem id={project.id} name={project.name} date={project.date} enterprise={project.enterprise} img={project.img} details={project.details}></BlockElem>
          </a>
        </div>
      )
      if((index+1) % 4 === 0) {
        finalArr.push(
          <div key={index} className="w3-row grid-section">{blocks}</div>
        )
        blocks = []
      }
    });

    if(isVisible){
      detailsBlock = <DetailsSection
                      name={projectName}
                      img={projectImg}
                      date={projectDate}
                      enterprise={projectEnterprise}
                      details={projectDetails}
                      isVisible={isVisible}
                      hideBlock={this.hideBlock}>
                    </DetailsSection>
      showGrid = 'showGrid'
    }else{
      detailsBlock = ''
      showGrid = ''
    }

    console.log(isVisible)

    return(
      <div className={`w3-container ${showGrid}`}>
        <h2 name="portfolio" id="portfolio">Portafolio</h2>
        {finalArr}
        {detailsBlock}
      </div>
    )
  }
}

